.simon {
  height: 100%;
  min-height: -webkit-fill-available;
  max-width: 500px;
  margin: auto;
  position: relative;
}

.container {
  height: 100%;
  width: 100%;
  flex-direction: column;
  display: flex;
  background: #f7f7f7;
}

.option {
  height: 100%;
  margin: 20px;
  border: 5px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  cursor: pointer;

  &:nth-child(1) {
    background: #ef476f;
  }
  &:nth-child(2) {
    background: #ffd166;
  }
  &:nth-child(3) {
    background: #06d6a0;
  }
  &:nth-child(4) {
    background: #118ab2;
  }
  &:nth-child(5) {
    background: #073b4c;
  }
  &:nth-child(6) {
    background: #7678ed;
  }

  &:hover {
    opacity: 0.8;
  }
  &.active,
  &:active {
    opacity: 0.3;
  }
}

.screen {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background: #f7f7f7;
}
